  font-weight: bold;
  margin-right: 50px;
}

.app-header .user-block,
.app-header .menu-button {
  margin-left: auto;
}

.menu-toggle {
  display: none;
}

.app-header .menu-button {
  display: none;
  width: 32px;
  font-weight: 900;
  font-size: 24px;
  line-height: 28px;
  height: 30px;
  background: #673cad;
  text-align: center;
  border-radius: 4px;
  position: relative;
  -webkit-transition: background 0.5s;
  transition: background 0.5s;
  cursor: pointer;
}

.menu-button:before {
  content: "\2630";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
}

.menu-toggle:checked ~ .menu-button {
  background: #9c27b0;
}
.menu-toggle:checked ~ .menu-button:before {
  content: "\2716";
}

.menu-toggle:checked ~ .user-block {
  right: 0;
}

@media screen and (max-width: 830px) {
  .app-header .user-block {
    width: 240px;
    position: fixed;
    top: 70px;
    bottom: 0;
    right: -550px;
    -webkit-transition: right 0.8s ease-in-out;
    transition: right 0.8s ease-in-out;
    padding: 20px;
    box-sizing: border-box;
