.pagination__link {
  display: inline-block;
  padding: 10px;
  text-align: center;
  background: #fff;
  border: 1px solid #ccc;
  border-left-width: 0;
  position: relative;
}

.pagination__link:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0px;
  border-left: 1px solid transparent;
}

.pagination__link .link-text {
  padding: 0 10px;
