  z-index: 100;
}

.preloader-wrapper {
  text-align: center;
  color: #ffffff;
  font-weight: bold;
}

.preloader {
  fill: none;
  stroke-width: 8px;
  stroke: #fff;
  stroke-dasharray: 40;
  stroke-dashoffset: 50%;
  -webkit-animation: movedash 1.2s ease-in-out forwards 0s infinite;
          animation: movedash 1.2s ease-in-out forwards 0s infinite;
}

@-webkit-keyframes movedash {
  to {
    stroke-dashoffset: 250%;
  }
}

@keyframes movedash {
  to {
    stroke-dashoffset: 250%;
  }
}

